import React, { useLayoutEffect, useMemo } from "react"
import {
  Input,
  InputGroup,
  InputLeftElement,
  InputRightElement,
  Box,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverTrigger,
  useMediaQuery,
} from "@chakra-ui/react"

import { ChevronRightIcon, Search2Icon } from "@chakra-ui/icons"
import { graphql, navigate, useStaticQuery } from "gatsby"
import { useFlexSearch } from "react-use-flexsearch"

function SearchInput() {
  const [searchPlaceholderSize] = useMediaQuery("(min-width: 1024px)")
  const [searchQuery, setSearchQuery] = React.useState("")
  const [searchInputFocused, setSearchInputFocused] = React.useState(false)
  const [width, setWidth] = React.useState(0)
  const searchInputRef = React.useRef()

  const queryData = useStaticQuery(graphql`
    query {
      localSearchPages {
        index
        store
      }
    }
  `)

  const index = queryData.localSearchPages.index
  const store = queryData.localSearchPages.store

  const searchResults = useFlexSearch(searchQuery, index, store)

  const handleSearchQueryChange = event => {
    setSearchQuery(event.target.value)
  }

  const isOpen = useMemo(
    () => searchQuery.length > 2 && searchInputFocused,
    [searchInputFocused, searchQuery]
  )

  useLayoutEffect(() => {
    setWidth(searchInputRef?.current?.clientWidth)
  }, [])

  return (
    <Popover
      isOpen={isOpen}
      initialFocusRef={searchInputRef}
      gutter={0}
      matchWidth
    >
      <PopoverTrigger>
        <InputGroup>
          <InputLeftElement>
            <Search2Icon boxSize={5} />
          </InputLeftElement>
          <Input
            bg="white"
            fontSize={"1.375rem"}
            placeholder={
              searchPlaceholderSize ? "Search our documentation" : "Search"
            }
            color="rlBlack"
            minH={"2.3rem"}
            value={searchQuery}
            onChange={handleSearchQueryChange}
            onFocus={() => setSearchInputFocused(true)}
            onBlur={() => setSearchInputFocused(false)}
            ref={searchInputRef}
            borderBottomRadius={isOpen ? "none" : null}
          />
          <InputRightElement>
            <ChevronRightIcon boxSize={7} />
          </InputRightElement>
        </InputGroup>
      </PopoverTrigger>
      <PopoverContent
        borderRadius={"none"}
        borderTopWidth={3}
        borderTopColor={"rlBlack"}
        w={width}
      >
        <PopoverBody>
          {searchResults.length === 0 ? (
            <Box px={1} rounded="sm">
              No results found
            </Box>
          ) : (
            searchResults.map(result => (
              <Box
                key={result.id}
                onClick={() => {
                  navigate(`/${result.path}`)
                }}
                _hover={{
                  background: "rlBlack20",
                }}
                cursor="pointer"
                px={1}
                rounded="sm"
              >
                <Box
                  mt="1"
                  fontWeight="semibold"
                  as="h4"
                  lineHeight="tight"
                  noOfLines={1}
                >
                  {result.title}
                </Box>
                {result.description && (
                  <Box as="span" color="gray.600" fontSize="sm">
                    {result.description}
                  </Box>
                )}
              </Box>
            ))
          )}
        </PopoverBody>
      </PopoverContent>
    </Popover>
  )
}

export default SearchInput
