import {
  Box,
  Flex,
  Text,
  IconButton,
  Stack,
  Collapse,
  Icon,
  Link,
  Popover,
  PopoverTrigger,
  PopoverContent,
  useDisclosure,
} from "@chakra-ui/react"
import {
  HamburgerIcon,
  CloseIcon,
  ChevronDownIcon,
  TriangleDownIcon,
} from "@chakra-ui/icons"
import React from "react"
import Logo from "../logo"
import Dropdown from "../dropdown/dropdown"
import SearchInput from "./searchInput/searchInput"

const Header = ({ tocContents }) => {
  const { isOpen, onToggle } = useDisclosure()

  const root = tocContents?.nodes.map(x => ({
    title: x.name,
    href: x.name,
  }))

  const roots = root
    ?.filter(x => x.title !== "TOC")
    ?.sort((a, b) => (a.title > b.title ? 1 : -1))

  const dropdownData = [
    {
      title: "Documentation",
      children: roots ?? [],
    },
  ]

  return (
    <Box>
      <Box bg="rlBlack" justifyContent="space-between" align="center">
        <Flex
          bg="rlBlack"
          minH={"5.25rem"}
          px={{ base: 6 }}
          align="center"
          maxW="90rem"
          justifyItems="center"
        >
          <Flex flex={{ base: 4 }}>
            <Box
              display={{ base: "none", md: "flex" }}
              mr="2rem"
              data-testid="logo"
            >
              <Logo />
            </Box>
            <Stack
              spacing={8}
              alignItems={"left"}
              maxW={"37.5rem"}
              width={{
                base: "100%",
                md: "80%",
                lg: "70%",
                xl: "100%",
                "2xl": "100%",
              }}
            >
              <SearchInput />
            </Stack>
          </Flex>

          <Stack justify={"flex-end"} direction={"row"} spacing={6} pl={5}>
            <Flex
              display={{ base: "none", md: "flex" }}
              mr={2}
              alignItems={"center"}
            >
              <DesktopNav data={dropdownData} />
            </Flex>
          </Stack>
          <Flex
            flex={{ base: 0.3, md: "auto" }}
            mr={{ base: -2 }}
            display={{ base: "flex", md: "none" }}
            pl={5}
            data-testid="menu-button"
          >
            <IconButton
              onClick={onToggle}
              icon={
                isOpen ? (
                  <CloseIcon w={3} h={3} />
                ) : (
                  <HamburgerIcon w={5} h={5} />
                )
              }
              aria-label={"Toggle Navigation"}
            />
          </Flex>
        </Flex>
      </Box>

      <Collapse in={isOpen} animateOpacity>
        <MobileNav data={dropdownData} />
      </Collapse>
    </Box>
  )
}

const DesktopNav = ({ ...props }) => {
  const { isOpen, onToggle } = useDisclosure()
  return (
    <Stack direction={"row"} spacing={4} alignItems={"center"}>
      {props.data.map(navItem => (
        <Flex key={navItem.title} onClick={onToggle}>
          <Popover trigger={"hover"} placement={"bottom-start"}>
            <PopoverTrigger>
              <Link
                p={1}
                href={navItem.href ?? "#"}
                fontSize={"xl"}
                fontWeight={500}
                color={"rlWhite"}
                _hover={{
                  textDecoration: "none",
                  color: "rlYellow",
                }}
              >
                <Flex
                  alignItems={"center"}
                  data-testid="dropdown-title"
                  fontSize={24}
                >
                  {navItem.title}
                  <TriangleDownIcon
                    boxSize={3}
                    color={"white"}
                    ml={3}
                    transition={"all .25s ease-in-out"}
                    transform={isOpen ? "rotate(180deg)" : ""}
                  />
                </Flex>
              </Link>
            </PopoverTrigger>
            {navItem.children && (
              <PopoverContent
                boxShadow={"xl"}
                borderRadius={0}
                bg={"white"}
                p={1}
                style={{ marginTop: "0!important" }}
              >
                <Stack m={2}>
                  {navItem.children.map(child => (
                    <DesktopSubNav key={child.title} {...child} />
                  ))}
                </Stack>
              </PopoverContent>
            )}
          </Popover>
        </Flex>
      ))}
      <Dropdown title={"APIs"}>Dummy data</Dropdown>
      <Dropdown as={Box} title={"V7.4"}>
        Dummy data
      </Dropdown>
    </Stack>
  )
}

const DesktopSubNav = ({ title, href }) => {
  return (
    <Link
      href={`/${href}`}
      role={"group"}
      display={"block"}
      p={1}
      _hover={{ bg: "rlYellow" }}
    >
      <Stack direction={"row"} align={"center"}>
        <Text
          transition={"all .3s ease"}
          fontWeight={600}
          style={{ textTransform: "capitalize" }}
        >
          {title}
        </Text>
      </Stack>
    </Link>
  )
}

const MobileNav = ({ ...props }) => {
  return (
    <Stack
      bg="rlBlack20"
      p={4}
      display={{ md: "none" }}
      data-testid="mobile-title"
    >
      {props.data.map(navItem => (
        <MobileNavItem key={navItem.title} {...navItem} />
      ))}
    </Stack>
  )
}

const MobileNavItem = ({ children, ...props }) => {
  const { isOpen, onToggle } = useDisclosure()

  return (
    <Stack spacing={4} onClick={children && onToggle}>
      <Flex
        py={2}
        as={Link}
        href={props.href ?? "#"}
        justify={"space-between"}
        align={"center"}
        _hover={{
          textDecoration: "none",
        }}
      >
        <Text fontWeight={600} color={"rlBlack"}>
          {props.title}
        </Text>
        {children && (
          <Icon
            as={ChevronDownIcon}
            transition={"all .25s ease-in-out"}
            transform={isOpen ? "rotate(180deg)" : ""}
            w={6}
            h={6}
          />
        )}
      </Flex>

      <Collapse in={isOpen} animateOpacity style={{ marginTop: "0!important" }}>
        <Stack
          mt={1}
          pl={4}
          borderLeft={5}
          borderStyle={"solid"}
          borderColor={"rlYellow"}
          align={"start"}
        >
          {children &&
            children.map(child => (
              <Link key={child.title} py={2} href={`/${child.href}`}>
                {child.title}
              </Link>
            ))}
        </Stack>
      </Collapse>
    </Stack>
  )
}

export default Header
